import { apiUrl, vuexPersistKey } from '@/shared/AppConfig'
import * as types from './auth.mutation-types'
import * as roles from '@/shared/Roles'
import axios from 'axios'

export default {
    state: {
        status: '',
        token: '',
        role: '',
        uid: '',
        username: ''
    },
    mutations: {
        [types.auth_request] : (state) => {
            state.status = 'loading'
        },
        [types.auth_success] : (state, payload) => {
            state.status = 'success',
            state.token = payload.token,
            state.role = payload.role,
            state.uid = payload.uid
            state.username = payload.username
        },
        [types.auth_error] : (state) => {
            state.status = 'error',
            state.token = '',
            state.role = '',
            state.uid = ''
            state.username = ''
        },
        [types.logout] : (state) => {
            state.status = '',
            state.token = '',
            state.role = '',
            state.uid = ''
            state.username = ''
        }
    },
    actions: {
        login: ({commit}, user) => {
            return new Promise((resolve, reject) => {
                commit(types.auth_request);
                axios.post(apiUrl + '/auth/sign-in', {
                    username: user.username,
                    password: user.password
                })
                .then(response => {
                    let payload = {
                        token: response.data.jwt,
                        role: setRole(response.data.roles[0]),
                        uid: response.data.id,
                        username: response.data.username
                    }
                    commit(types.auth_success, payload)
                    resolve(response);
                })
                .catch((error) => {
                    commit('auth_error');
                    reject(error);
                })
            })
        },
        logout: ({commit}) => {
            return new Promise((resolve) => {
                commit(types.logout);
                localStorage.removeItem(vuexPersistKey)
                resolve();
            })
        }
    },
    getters: {
        isLoggedIn: state => !!state.token,
        authStatus: state => state.status,
        getToken: state => state.token,
        getUid: state => state.uid,
        getUsername: state => state.username
    }
}

function setRole(_role) {
    let role = ''
    switch (_role) {
        case 'admin':
            role = roles.ADMIN
            break
        case 'dmp':
            role = roles.DMP
            break
        case 'lawyer':
            role = roles.LAWYER
            break
        case 'finance':
            role = roles.FINANCE
            break
        case 'sysadmin':
            role = roles.SYSADMIN
            break
        default:
            role = ''
    }
    return role
}