import Vue from 'vue'
import Router from 'vue-router'
import store from '@/stores/Store';

// Containers
const DefaultContainer = () => import('@/containers/admin')
const LoginContainer = () => import('@/containers/login')

// Views
const Dashboard = () => import('@/views/_default/Dashboard')

const EmployeeList = () => import('@/views/employee/list')
const EmployeeCreate = () => import('@/views/employee/create')
const EmployeeView = () => import('@/views/employee/view')
const ProfileView = () => import('@/views/profile/view')
const UploadCv = () => import('@/views/profile/upload-cv')
const ChangePassword = () => import('@/views/profile/change-password')
const EngagementList = () => import('@/views/engagement/list')
const EngagementCreate = () => import('@/views/engagement/create')
const EngagementView = () => import('@/views/engagement/view')
const EngagementApprovalList = () => import('@/views/engagement/approvalList')
const EngagementApprovalView = () => import('@/views/engagement/approvalView')
const LoanAList = () => import('@/views/loanA/list')
const LoanACreate = () => import('@/views/loanA/create')
const LoanAApprovalList = () => import('@/views/loanA/approvalList')
const LoanBList = () => import('@/views/loanB/list')
const LoanBCreate = () => import('@/views/loanB/create')
const LoanBApprovalList = () => import('@/views/loanB/approvalList')

Vue.use(Router)

const router = new Router({
    mode: 'hash',
    linkActiveClass: 'open active',
    scrollBehavior: () => ({ y: 0 }),
    routes: [
        {
            path: '*',
            redirect: '/login'
        },
        {
            path: '/login',
            name: 'Login',
            component: LoginContainer
        },
        {
            path: '/admin',
            redirect: '/admin/dashboard',
            name: 'Home',
            component: DefaultContainer,
            children: [
                {
                    path: '/admin/dashboard',
                    name: 'Dashboard',
                    component: Dashboard,
                    meta: {
                        label: 'Dashboard',
                        requiresAuth: true
                    }
                },
                {
                    path: '/employee',
                    redirect: '/admin/employee',
                    name: 'Employee',
                    component: {
                        render (c) { return c('router-view') }
                    },
                    children: [
                        {
                            path: '/admin/employee',
                            name: 'EmployeeList',
                            component: EmployeeList,
                            meta: {
                                label: 'EmployeeManagement',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/employee/create',
                            name: 'EmployeeCreate',
                            component: EmployeeCreate,
                            meta: {
                                label: 'Register New Employee',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/employee/:id',
                            name: 'EmployeeView',
                            component: EmployeeView,
                            meta: {
                                label: 'View Employee',
                                requiresAuth: true
                            }
                        },
                    ]
                },
                {
                    path: '/profile',
                    redirect: '/admin/profile',
                    name: 'Profile',
                    component: {
                        render(c) { return c('router-view') }
                    },
                    children: [
                        {
                            path: '/admin/profile',
                            name: 'ProfileView',
                            component: ProfileView,
                            meta: {
                                label: 'My Profile',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/profile/upload-cv',
                            name: 'UploadNewCV',
                            component: UploadCv,
                            meta: {
                                label: 'Upload New CV',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/profile/change-password',
                            name: 'ChangePassword',
                            component: ChangePassword,
                            meta: {
                                label: 'Change Password',
                                requiresAuth: true
                            }
                        }
                    ]
                },
                {
                    path: '/engagement',
                    redirect: '/admin/engagement',
                    name: 'Engagement',
                    component: {
                        render (c) { return c('router-view') }
                    },
                    children: [
                        {
                            path: '/admin/engagement',
                            name: 'EngagementList',
                            component: EngagementList,
                            meta: {
                                label: 'Engagement List',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/engagement/create',
                            name: 'EngagementCreate',
                            component: EngagementCreate,
                            meta: {
                                label: 'Create Engagement',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/engagement/view/:id',
                            name: 'EngagementView',
                            component: EngagementView,
                            meta: {
                                label: 'View Engagement',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/engagement/approval',
                            name: 'EngagementApprovalList',
                            component: EngagementApprovalList,
                            meta: {
                                label: 'Engagement Approval List',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/engagement/approval/:id',
                            name: 'EngagementApprovalView',
                            component: EngagementApprovalView,
                            meta: {
                                label: 'Engagement Approval',
                                requiresAuth: true
                            }
                        }
                    ]
                },
                {
                    path: '/loan-a',
                    redirect: '/admin/loan-a',
                    name: 'Loan A',
                    component: {
                        render(c) { return c('router-view') }
                    },
                    children: [
                        {
                            path: '/admin/loan-a',
                            name: 'LoanAList',
                            component: LoanAList,
                            meta: {
                                label: 'Loan A List',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/loan-a/create',
                            name: 'LoanACreate',
                            component: LoanACreate,
                            meta: {
                                label: 'Create Loan A',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/loan-a/approval-list',
                            name: 'LoanAApprovalList',
                            component: LoanAApprovalList,
                            meta: {
                                label: 'Loan A Approval List',
                                requiresAuth: true
                            }
                        },
                    ]
                },
                {
                    path: '/loan-b',
                    redirect: '/admin/loan-b',
                    name: 'Loan B',
                    component: {
                        render(c) { return c('router-view') }
                    },
                    children: [
                        {
                            path: '/admin/loan-b',
                            name: 'LoanBList',
                            component: LoanBList,
                            meta: {
                                label: 'Loan B List',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/loan-b/create',
                            name: 'LoanBCreate',
                            component: LoanBCreate,
                            meta: {
                                label: 'Create Loan B',
                                requiresAuth: true
                            }
                        },
                        {
                            path: '/admin/loan-b/approval-list',
                            name: 'LoanBApprovalList',
                            component: LoanBApprovalList,
                            meta: {
                                label: 'Loan B Approval List',
                                requiresAuth: true
                            }
                        }
                    ]
                },
                {
                    path: '*',
                    redirect: '/admin/dashboard'
                },
            ]
        }
    ]
})

// Login Guard
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (store.getters.isLoggedIn) {
          next()
          return
        }
    next('/login')
    } else {
        next()
    }
})

export default router;